.rules {

  &__list {
    @include list-reset;
    display: grid;
    justify-content: center;
    align-items: flex-start;
    gap: 10px 0;
  }

  &__item {
    border: 1px solid var(--silver);
    border-radius: 8px;
    box-shadow: 1px 2px 1px 0 var(--silver);
    min-width: 650px;
    max-width: 650px;

    @include media(768px) {
      min-width: 480px;
      max-width: 480px;
    }

    @include media(570px) {
      min-width: 360px;
      max-width: 360px;
    }

    @include media(470px) {
      min-width: 230px;
      max-width: 230px;
    }
  }

  &__title {
    position: relative;
    display: block;
    padding: 1rem 9rem 1rem 1rem;
    font-size: var(--medium);
    cursor: pointer;

    &::after {
      content: "";
      display: block;
      width: 20px;
      height: 20px;
      position: absolute;
      top: 50%;
      right: 10%;
      transform: translateY(-50%);
      background-image: url(../images/rules/arrow.svg);
      background-repeat: no-repeat;
      background-position: center;
      transition: transform 0.25s ease-in-out;
      transform-origin: center;
    }

    @include media(768px) {
      padding: 1rem 6rem 1rem 1rem;
    }

    @include media(570px) {
      padding: 1rem 3.5rem 1rem 1rem;
    }

    @include media(470px) {
      padding: 1rem 2.6rem 1rem 1rem;
    }
  }

  &__input:checked ~ &__content {
    display: block;
  }

  &__input:checked ~ &__title::after {
    top: 35%;
    transform: rotate(180deg);
  }

  &__input:focus-visible ~ &__title {
    outline: 1px solid var(--tundora);
  }

  &__content {
    display: none;
    margin: 0;
    padding: 0.5rem 1rem;
    font-size: var(--small);
    line-height: 1.4;
    border-top: 1px dashed var(--tundora);
  }
}
