.scroll {
  position: fixed;
  left: 5%;
  bottom: 10%;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  cursor: pointer;
  opacity: 0;
  visibility: hidden;
  box-shadow: 1px 2px 1px 0 var(--silver);
  transition: opacity 0.25s ease-in-out;
  color: var(--tundora);
  background-color: var(--seashell);
  border: 2px solid var(--tundora);

  @include media(540px) {
    width: 35px;
    height: 35px;
    left: 2%;
  }

  &::before {
    content: "â†‘";
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: var(--medium);
  }

  &--active {
    opacity: 1;
    visibility: visible;
  }
}
