.table {

  &__list {
    @include list-reset;
    padding: 1rem;
    border: 1px solid var(--silver);
    box-shadow: 1px 2px 1px 0 var(--silver);
    display: grid;
    gap: 0 2rem;
    grid-template-columns: repeat(3, 1fr);
  }

  &__item {
    text-align: center
  }

  &__column {
    @include list-reset;
    display: grid;
    gap: 1rem 0;
    margin-top: 2rem;
  }

  &__row-item {
    border: 1px solid var(--tundora);
    padding: 1.5rem;

    &:nth-child(2n):not(:last-child) {
      margin-bottom: 4rem;
    }
  }
}